<template>
  <div class="make-appointment">
    <div  v-if="!this.loading" class="container">
      <div class="appointment-list">
        <h3>{{ this.availableDates[this.dateIndex].text }}</h3>
        
        <div v-for="appointment in this.todaysAppointments" v-if="this.todaysAppointments.length > 0">
          <div class="appointment">
            <span>{{ this.timeSlots[appointment.start_index] + " - " + this.timeSlots[appointment.end_index] }}</span><span>{{appointment.name}}</span>
          </div>
        </div>
        <div v-else>
          <div class="no-appointment">
            <h5>{{ this.availableDates[this.dateIndex].text.substring(0, this.availableDates[this.dateIndex].text.length - 7) }} vēl neviens nav pieteicies</h5>
          </div>
        </div>
      </div>

      
      <div class="selectors">
        <h2>Piesakies:</h2>
        <form @submit.prevent="createAppointment()">
          <input v-model="name" type="text" name="name" placeholder="Vārds, uzvārds" required>
          <input v-model="phone" type="tel" name="phone" placeholder="Telefona numurs" required>
          <select v-model="dateIndex" v-on:change="this.time = Object.keys(this.currentTimeSlots[this.dateIndex])[0]">
            <option v-for="(date, index) in this.availableDates" :value="index">{{ this.availableDates[index].text }}</option>
          </select>
          <select v-model="duration" v-on:change="this.time = Object.keys(this.currentTimeSlots[this.dateIndex])[0]">
            <option v-for="(value, key) in this.durations" :value="key">{{ value }}</option>
          </select>
          <select v-if="Object.keys(this.currentTimeSlots[this.dateIndex]).length > 0" v-model="time">
            <option v-for="(value, key) in this.currentTimeSlots[this.dateIndex]" :value="key">{{ value }}</option>
          </select>
          <select v-else disabled>
            <option :value="-1" selected>Visi laiki ir aizņemti</option>
          </select>
          <label class="error-message">{{ this.errorMessage }}</label>
          <input class="btn" type="submit" value="Pieteikties">
        </form>
      </div>
      <footer><a href="tel:+37127804609">&phone; Atis Ozols</a></footer>
    </div>
    <div v-else class="spinner">
      <div class="loadingio-spinner-rolling-m1e8ui1q0jb"><div class="ldio-ood1y432iu">
      <div></div>
      </div></div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
import { collection, query, where, getDocs, orderBy, addDoc } from 'firebase/firestore'

export default {
  name: 'Home',
  data () {
    return {
      loading: true,
      errorMessage: '',
      name: '',
      phone: '',
      appointments: [],
      dateIndex: 0,
      duration: 3,
      prevDuration: 3,
      time: 0,
      timeSlots: {
        0: '06:30', 1: '06:45', 2: '07:00', 3: '07:15', 4: '07:30', 5: '07:45', 6: '08:00', 7: '08:15', 8: '08:30', 9: '08:45', 10: '09:00', 11: '09:15', 12: '09:30', 13: '09:45', 14: '10:00', 15: '10:15', 16: '10:30', 17: '10:45', 18: '11:00', 19: '11:15', 20: '11:30', 21: '11:45', 22: '12:00', 23: '12:15', 24: '12:30', 25: '12:45', 26: '13:00', 27: '13:15', 28: '13:30', 29: '13:45', 30: '14:00', 31: '14:15', 32: '14:30', 33: '14:45', 34: '15:00', 35: '15:15', 36: '15:30', 37: '15:45', 38: '16:00', 39: '16:15', 40: '16:30', 41: '16:45', 42: '17:00', 43: '17:15', 44: '17:30', 45: '17:45', 46: '18:00', 47: '18:15', 48: '18:30', 49: '18:45', 50: '19:00', 51: '19:15', 52: '19:30', 53: '19:45', 54: '20:00', 55: '20:15', 56: '20:30', 57: '20:45', 58: '21:00', 59: '21:15', 60: '21:30', 61: '21:45', 62: '22:00', 63: '22:15', 64: '22:30', 65: '22:45', 66: '23:00', 67: '23:15', 68: '23:30', 69: '23:45', 70: '00:00'
      },
      durations: {
        3: '45 min',
        4: '60 min',
        5: '75 min',
        6: '90 min'
      },
      dayDict: {
        'Monday': 'Pirmdien',
        'Tuesday': 'Otrdien',
        'Wednesday': 'Trešdien',
        'Thursday': 'Ceturtdien',
        'Friday': 'Piektdien',
        'Saturday': 'Sestdien',
        'Sunday': 'Svētdien'
      }
    }
  },
  methods:{
    createAppointment(){
      if(Object.keys(this.currentTimeSlots[this.dateIndex]).length < 1){
        this.time = -1
      }

      if(this.name.length < 8 || this.phone.length < 8){
        this.errorMessage = 'Lūdzu, aizpildi visus laukus!'
      }

      if(this.time == -1){
        this.errorMessage = 'Lūdzu, izvēlies laiku!'
      }

      else{
        this.errorMessage = ''

        const appointmentDoc = {
        name: this.name,
        phone: this.phone,
        date: this.availableDates[this.dateIndex].value,
        start_index: parseInt(this.time),
        end_index: parseInt(this.time)+parseInt(this.duration)
        }

        this.loading = true

        addDoc(this.appointmentsCollection, appointmentDoc)
        .then(() => {
            console.log("Document has been added successfully");
            this.readAppointments()
        })
        .catch(error => {
            console.log(error);
        })

        this.name = ''
        this.phone = ''
        this.duration = 3
        this.time = Object.keys(this.currentTimeSlots[this.dateIndex])[0]

        this.loading = false
      }
    },
    async readAppointments(){
      const q = query(this.appointmentsCollection, where("date", ">=", this.availableDates[0].value), orderBy('date'), orderBy('start_index'))
      const appointmentsSnapshot = await getDocs(q);
      this.appointments = []
      appointmentsSnapshot.forEach(doc => {
        this.appointments.push(doc.data())
      });
    }
  },
  mounted() {
    if (this.availableDates.length > 0) {
      this.dateIndex = 0;
    }

    this.readAppointments().then(() =>{
      this.loading = false
    })

    this.time = this.currentTime

    
  },
  created(){

  },
  watch:{
    currentTime(val){
      this.time = val
    },
    currentTimeSlots(val){
      console.log('Dropdown updated!')
    },
    duration(newDuration, old){
      this.prevDuration = old
    },
    currentDaySlots(val){
      const slots = []
      for(const key of Object.keys(this.currentTimeSlots[this.dateIndex])){
        slots.push(parseInt(key))
      }
      this.time = Math.min(...slots)
    }
  },
  computed:{
    availableDates() {
        const today = new Date();
        const next7Days = [];

        for (let i = 0; i < 7; i++) {
          const currentDate = new Date(today);
          currentDate.setDate(today.getDate() + i);
          
          const dateKey = currentDate.toLocaleString('en-US', { weekday: 'long'});
          const dateValue = currentDate.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' });
          const [month, day, year] = dateValue.split('/');
          const dateVal = parseInt(year+month+day)
          const europeanDateKey = `${day}/${month}`;
          
          if (i == 0){
            next7Days.push({text: 'Šodien' + ' (' + europeanDateKey + ')', value: dateVal });
          } else{
            if (i == 1){
              next7Days.push({text: 'Rītdien' + ' (' + europeanDateKey + ')', value: dateVal });
            }
            else{
              next7Days.push({text: this.dayDict[dateKey] + ' (' + europeanDateKey + ')', value: dateVal });
            }
          }
          
        }
        return next7Days;
    },
    currentTime(){
      // Get the current time in hours and minutes
      const now = new Date();
      const currentHours = now.getHours();
      const currentMinutes = now.getMinutes();
      const totalMinutes = (currentHours * 60 + currentMinutes) - 390;

      var slotIndex = Math.floor(totalMinutes / 15) + 1;
      // console.log('si', slotIndex, Object.keys(this.currentTimeSlots[this.dateIndex]))]

      if (slotIndex < 0) {
        slotIndex = Object.keys(this.currentTimeSlots[this.dateIndex])[0]
      }

      if(Object.keys(this.currentTimeSlots[this.dateIndex]).length < 1){
        slotIndex = -1
      }
      return slotIndex
    },
    currentTimeSlots() {
      const timeTable = []
      for(let i = 0; i < 7; i++){
        timeTable.push({...this.timeSlots})
        delete timeTable[i][70]
      }
      
      for(let i = 0; i < 7; i++){
        for (const appointment of this.appointments) {
          if(this.availableDates[i].value == appointment.date){
            const startKey = appointment.start_index;
            const endKey = appointment.end_index;

            for(let j = startKey; j < endKey; j++){
              delete timeTable[i][j]
            }
          }
          
        }
      }

      for(let i = 0; i < this.currentTime; i++){
        delete timeTable[0][i]
      }

      for (let i = 0; i < 7; i++) {
        for (const key in timeTable[i]) {
          for(let k = 1; k < this.duration; k++){
            if(!timeTable[i][parseInt(key) + parseInt(k)]){
              delete timeTable[i][key]
              break
            }
            

          }
        }
      }

      if (this.duration < this.prevDuration) {
        if(this.currentTime){
          return
        }
        else{
          console.log('currentTime error')
        }
      }

      return timeTable
    },
    todaysAppointments(){
      const appointmentList = []
      for (const appointment of this.appointments) {
          if(this.availableDates[this.dateIndex].value == appointment.date){
            appointmentList.push(appointment)
          }
      }
      return appointmentList
    },
    appointmentsCollection(){
      return collection(db, 'appointments')
    },
    currentDaySlots(){
      return Object.keys(this.currentTimeSlots[this.dateIndex])
    }
  }
  
}
</script>

